const generateAuthError = (message: string) => {
  switch (message) {
    case 'EMAIL_EXISTS':
      return '   email  ';
    case 'USER_DISABLED':
      return '    .';
    case 'EMAIL_NOT_FOUND':
    case 'INVALID_PASSWORD':
    case 'INVALID_EMAIL':
      return ' email  ';
    default:
      return '   ,  ';
  }
};

export default generateAuthError;
