import React from 'react';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import BookmarkBorderIcon from '@mui/icons-material/BookmarkBorder';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import { OverridableComponent } from '@mui/material/OverridableComponent';
import { SvgIconTypeMap } from '@mui/material';

const Login = React.lazy(() => import('../layouts/login'));
const Main = React.lazy(() => import('../layouts/main'));
const Profile = React.lazy(() => import('../layouts/profile'));
const Rooms = React.lazy(() => import('../layouts/rooms'));

export const userProfileRoutes = [
  { path: '/profile/booking', name: ' ', icon: StarBorderIcon },
  { path: '/profile/likes', name: '', icon: FavoriteBorderIcon },
  { path: '/profile/favorites', name: '', icon: BookmarkBorderIcon },
];

export type RoutesNavType = {
  path: string;
  name: string;
  icon?: OverridableComponent<SvgIconTypeMap<{}, 'svg'>> & {
    muiName: string;
  };
};

export const adminRoutes = [
  { path: '/profile/dashboard', name: ' ', icon: AdminPanelSettingsIcon },
  { path: '/profile/booking', name: ' ', icon: StarBorderIcon },
  { path: '/profile/likes', name: '', icon: FavoriteBorderIcon },
  { path: '/profile/favorites', name: '', icon: BookmarkBorderIcon },
];

export const navigationRoutes = [
  { path: '/rooms', name: ' ' },
  { path: '/services', name: '' },
  { path: '/vacancy', name: '' },
  { path: '/news', name: '' },
  { path: '/agreement', name: '' },
];

export const publicRoutes = [
  { path: '/login/:type?', component: Login },
  { path: '/rooms/:roomId?/', component: Rooms },
  { path: '/', component: Main, exact: true },
];

export const privateRoutes = [{ path: '/profile/:userId?/:route?', component: Profile, exact: true }];
